import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-accelerometer',
  templateUrl: './accelerometer.component.html',
  styleUrls: ['./accelerometer.component.css']
})
export class AccelerometerComponent implements OnInit {
  x: number = 0
  y: number = 0
  isGyro: boolean = true;
  constructor() {

  }
  ngOnInit() {

    var gyroPresent = false;
    var x = 0;
    var y = 0;
    window.addEventListener("devicemotion", function (event) {
      if (event.rotationRate.alpha || event.rotationRate.beta || event.rotationRate.gamma){
        gyroPresent = true;
      }
      console.log(gyroPresent);

      x = event.acceleration.x
      y = event.acceleration.y
    });
    this.isGyro = gyroPresent;
    this.x = x
    this.y = y


    var ball = document.querySelector('.ball');
    var garden = document.querySelector('.garden');
    var output = document.querySelector('.output');

    var maxX = garden.clientWidth - ball.clientWidth;
    var maxY = garden.clientHeight - ball.clientHeight;

    function handleOrientation(event) {
      var x = event.beta;  // In degree in the range [-180,180]
      var y = event.gamma; // In degree in the range [-90,90]

      output.innerHTML = "beta : " + x + "\n";
      output.innerHTML += "gamma: " + y + "\n";

      // Because we don't want to have the device upside down
      // We constrain the x value to the range [-90,90]
      if (x > 90) { x = 90 };
      if (x < -90) { x = -90 };

      // To make computation easier we shift the range of 
      // x and y to [0,180]
      x += 90;
      y += 90;

      console.log((maxX * x / 180 - 10) + "px")
      console.log((maxY * y / 180 - 10) + "px")
    }

    window.addEventListener('deviceorientation', handleOrientation);

  }


}
